.wrapper_banner{
      width: 100%;
      height: 34rem;
      position: absolute;
      top: 0;
      left: 0;
}

.banner_img{
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      position: absolute;
      overflow: hidden;
}

.banner_img:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(0,0,0,.3), var(--bg) 90%);
      top: 0;
      left: 0;
}

.wrapper_banner .container{
      height: 34rem;
      padding: 3rem;
}