.friends_block{
      width: 37rem;
      height: 12rem;
      margin-top: 4rem;
}

.friend_card{
      width: 11rem;
      height: 11rem;
      position: relative;
      border-radius: .4rem;
      margin: 0 1rem;
      display: flex;
      align-items: center;
      justify-content:center;
      overflow: hidden;
}

.friend_card:before{
      width: 100%;
      height: 100%;
      background: repeating-conic-gradient(from var(--a), var(--theme) 0%, var(--theme) 5%, transparent 5%, transparent 40%, var(--theme) 50%);
      animation: spin_photo 4s linear infinite;
      top: 0;
      left: 0;
      border-radius: .4rem;

}

.friend_card:before{
      width: 10.4rem;
      height: 10.4rem;
      top: .3rem;
      left: .3rem;
}

.friend_card a{
      width: 10rem;
      height: 10rem;
      position: relative;
      transform: translate(.5rem, .5rem);
      overflow: hidden;
}

.friend_card a img{
      width: 10rem;
      height: 10rem;
      border-radius: .4rem;
}

.friend_card a strong{
      position: absolute;
      width: 100%;
      left: 0;
      bottom: .5rem;
      background: var(--theme);
      text-transform: uppercase;
      color: var(--light);
      text-align: center;
      font-size: 1.4rem;
      padding: .5rem 0;
}

.friends_block button.slick-arrow{
      display: none;
      visibility: hidden;
}

.friends_block .slick-dots li{
      background: var(--light);
      width: .5rem;
      height: .5rem;
      border-radius: 50%;
}

.friends_block .slick-dots li.slick-active{
      background: var(--theme);
}