.main_content{
      position: relative;
      z-index: 10;
      width: 100%;
      height: auto;
      min-height: 108rem;
      margin-top: 14.8rem;
}

.main_content .container{
      padding: 0 5rem;
      gap: 3rem;
}

.main_content .container:before{
      width: 100%;
      height: 20rem;
      background: var(--bg);
      border-radius: 5rem 5rem 0 0;
      top: 9rem;
      left: 0;
      z-index: -1;
}

.user_data{
      gap: 2rem;
}

.user_photo{
      width: 19rem;
      height: 19rem;
      position: relative;
}

.user_photo__img{
      width: 19rem;
      height: 19rem;
      position: relative;
      align-items: center;
      justify-content: center;
}

.user_photo__img:before{
      width: 100%;
      height: 100%;
      background: repeating-conic-gradient(from var(--a), var(--theme) 0%, var(--theme) 5%, transparent 5%, transparent 40%, var(--theme) 50%);
      animation: spin_photo 4s linear infinite;
      border-radius: .4rem;
}

@property --a{
      syntax: '<angle>';
      inherits: false;
      initial-value: 0deg;
}

@keyframes spin_photo{
      0%{
            --a: 0deg;
      }
      100%{
            --a: 360deg;
      }
}

.user_photo__img:after{
      width: 18.6rem;
      height: 18.6rem;
      background: var(--bg);
      top: .2rem;
      left: .2rem;
      border-radius: .4rem;
}

.user_photo__img img{
      width: 18rem;
      height: 18rem;
      border-radius: .4rem;
      position: relative;
      z-index: 2;
}

.user_photo a{
      width: 4.2rem;
      height: 4.2rem;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--theme);
      left: calc(50% - 2.1rem);
      bottom: -2.1rem;
      z-index: 10;
      border-radius: .4rem;
}

.user_photo a *{
      color: var(--light);
      font-size: 2.4rem;
}

.user_info__title{
      position: relative;
}

.user_info__title h1{
      font-family: var(--ft-2);
      font-size: 6rem;
      font-weight: 600; 
      color: var(--theme);
}

.user_info__title strong{
      font-size: 2.5rem;
      position: absolute;
      top: -.5rem;
      left: 7rem;
}

.user_info p{
      font-size: 1.6rem;
}

.location{
      align-items: center;
      margin-top: 2rem;
      gap: .5rem;
}

.location *{
      color: var(--theme);
      font-size: 2.5rem;
}

.location span{
      font-size: 1.6rem;
}

.user_about{
      margin-top: 5rem;
}

.user_about h2{
      color: var(--theme);
      font-size: 2.4rem;
}

.user_about__text{
      margin-top: 2rem;
}

.user_about__text p{
      font-size: 1.6rem;
}

.user_social{
      margin-top: 3rem;
      gap: 1rem;
      align-items: center;
}

.wrapper_friends{
      margin-top: 5rem;
      flex-direction: column;
      align-items: center;
}

.wrapper_friends h2{
      font-size: 2.4rem;
      color: var(--theme);
}

/* relation */

.main_info{
      flex-direction: column;
      align-items: center;
}

.wrapper_relation{
      width: 100%;
      margin-top: 14rem;
      padding: 1rem;
      border: solid .1rem var(--border);
      border-radius: 1rem;
      position: relative;
      gap: 1rem;
}

.wrapper_relation a{
      width: 4rem;
      height: 4rem;
      background: var(--theme);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: absolute;
      toP: 0;
      right: 0;
      transform: translate(50%, -50%);
}

.wrapper_relation a *{
      color: var(--light);
      font-size: 1.6rem;
}

.relation_avatar,
.relation_avatar img{
      width: 10rem;
      height: 10rem;
      border-radius: .4rem;
}

.relation_avatar{
      background: var(--theme);
}

.relation_data h3{
      font-size: 2.4rem;
      color: var(--theme);
}

.relation_data strong{
      font-size: 1.6rem;
}

.relation_data p{
      margin-top: 1rem;
      font-size: 1.4rem;
}

.footer_content{
      flex-direction: column;
      align-items: center;
      margin-top: 5rem;
      display: none;
}

.footer_content *{
      font-size: 1.2rem;
      opacity: .5;
}