.wrapper_online{
      align-items: center;
      gap: .5rem;
      position: absolute;
      top: 5rem;
      right: 0;
}

.online_avatar,
.online_avatar img{
      width: 3.6rem;
      height: 3.6rem;
      border-radius: 50%;
}

.online_avatar{
      background: var(--bg);
      position: relative;
}

.online_dots{
      width: 1.4rem;
      height: 1.4rem;
      position: absolute;
      background: var(--theme);
      border: solid .2rem var(--bg);
      border-radius: 50%;
      top: 0;
      right: -.2rem;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red-200);
}

.online_status{
      background: rgba(0,0,0,.5);
      padding: .3rem 1.5rem;
      border-radius: 999px;
      text-transform: uppercase;
      font-size: 1.2rem;
      font-weight: 500;
}

.online_status.red{
      color: var(--red-200);
}

.online_status.green{
      color: var(--green);
}