@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@100;200;300;400;600;700&family=Space+Grotesk:wght@300;400;500&display=swap');

:root{
    --ft-1: 'Kanit', sans-serif;
    --ft-2: 'Space Grotesk', sans-serif;

    /** colors */
    --green: #45d985;
    --light: #fff;
    --red-200: #d11e22;
    --red-100: #e90d0f;
    --alpha: rgba(0,0,0,.1 );
    --grys-300: #a2c2c8;
    --grys-200: #c9d8db;
    --grys-100: #fcffff;

    /** default theme */
    /* kimy*/
    --theme: #8c6bb1;
    --text: #bebada;

    /* married*/
    
    --theme: #0868ac;
    --text: #bdcce4;
    
    --border: rgba(71,73,160,0.17);
    --bg: #071334;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #1da1f2;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;

    /** globals */
    font-family: var(--ft-2);
    font-size: 62.5%;
    color: var(--text);

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bg);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

.flex{
  display: flex;
}

.grid{
  display: grid;
}

.col-2{
  grid-template-columns: repeat(2, 1fr);
}

.container{
  width: 96%;
  max-width: 108rem;
  margin: 0 auto;
  height: auto;
  position: relative;
}

::-webkit-scrollbar{
  width: .8rem;
  background: rgba(0,0,0,.05);
  border-radius: .8rem;
}

::-webkit-scrollbar-thumb{
  background: var(--theme);
  border-radius: .8rem;
}